@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.App{margin:0;font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased !important}

.navbar-brand{font-weight:600;letter-spacing:1px;text-transform:uppercase;cursor:pointer}.navbar-brand:hover{color:#ffc952 !important}.navbar-nav .nav-item{letter-spacing:1px;margin-left:20px;text-align:center;text-transform:uppercase;cursor:pointer}.navbar-nav .nav-item:hover{color:#ffc952 !important}.navbar-nav a{color:#212529 !important;display:inline;font-size:1rem}.navbar-nav a:hover{color:#ffc952 !important}.navbar-nav svg{font-size:2em}

.table .row{justify-content:center}.table thead>tr:first-child{display:none}.table .nav-link{padding:0}.table .Easy{background-color:green}.table .Medium{background-color:orange}.table .Hard{background-color:red}

